import React, { useState, useEffect } from 'react'
import {Link} from 'react-router-dom';
import nfts from 'config/constants/newnfts'
import NftList from './components/NftList'
import NftProvider from './contexts/NftProvider'

const VLADCollection = () => {
  const [NFTs, setNFTs] = useState(nfts)

  const filterNFTs = (rarity: string) => {
    const filteredNFTs = nfts.filter((nft) => nft.rarity === rarity)
    setNFTs(filteredNFTs)
  }

  useEffect(() => {
    filterNFTs('Base')
  }, [])
  return (
    <NftProvider>
      <section className="vcollectionsec">
      <div className="container">
        <div className="row">
          <div className="col-sm-12 col-md-12 mb-5 vchead">
            <h3>VLAD NFT Facts</h3>
            <div className="vctab-wrap">
              <ul className="vctab nav nav-pills mb-3" id="pills-tab" role="tablist">
                <li className="nav-item" role="presentation">
                  <button className="nav-link vclink active" id="pills-home-tab" data-bs-toggle="pill" data-bs-target="#pills-home" type="button" role="tab" aria-controls="pills-home" aria-selected="true">ALL</button>
                </li>
                <li className="nav-item" role="presentation">
                  <button className="nav-link vclink" id="pills-profile-tab" data-bs-toggle="pill" data-bs-target="#pills-profile" type="button" role="tab" aria-controls="pills-profile" >FOR SALE</button>
                </li>
                <li className="nav-item" role="presentation">
                  <button className="nav-link vclink" id="pills-contact-tab" data-bs-toggle="pill" data-bs-target="#pills-contact" type="button" role="tab" aria-controls="pills-contact" >BID</button>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-12 col-md-12">
            <div className="pricefilterwrap">
              <ul className="nav nav-pills pricefilterpills mb-3" id="pills-tab" role="tablist">
                <li className="nav-item" role="presentation">
                  <button className="nav-link pricelink active" id="pills-home-tab" data-bs-toggle="pill" data-bs-target="#price-lowest" type="button" role="tab" aria-controls="pills-home" aria-selected="true">Price - Lowest</button>
                </li>
                <li className="nav-item" role="presentation">
                  <button className="nav-link pricelink" id="pills-profile-tab" data-bs-toggle="pill" data-bs-target="#price-highest" type="button" role="tab" aria-controls="pills-profile" aria-selected="false">Price - Highest</button>
                </li>
                <li className="nav-item" role="presentation">
                  <button className="nav-link pricelink" id="pills-contact-tab" data-bs-toggle="pill" data-bs-target="#rarity-rare" type="button" role="tab" aria-controls="pills-contact" aria-selected="false">Rarity - Rare</button>
                </li>
                <li className="nav-item" role="presentation">
                  <button className="nav-link pricelink" id="pills-contact-tab" data-bs-toggle="pill" data-bs-target="#rarity-common" type="button" role="tab" aria-controls="pills-contact" aria-selected="false">Rarity - Common</button>
                </li>
              </ul>
            </div>
            <div className="vc-filter vc-explore">
              <ul className="nav vc-nav nav-pills">
                <li className="nav-item vcdropdown pricedropdown dropdown">
                  <Link className="nav-link vcdropdown-toggle dropdown-toggle dropbtn" data-bs-toggle="dropdown" to="/" role="button" aria-expanded="false">Filter <img src="assets/images/settings-filter.svg" alt="filter-icon" className="img-fluid" /></Link>
                  <div className="dropdown-content">
                    <p>Format</p>
                    <Link to="/">
                      <div className="form-check checkblock">
                          <input className="form-check-input" type="checkbox" id="flexCheckDefault" />
                        <label className="form-check-label" htmlFor="flexCheckDefault">
                          GIF
                          </label>
                      </div>
                      <div className="count">23</div>
                    </Link>
                    <Link to="/">
                      <div className="form-check checkblock">
                          <input className="form-check-input" type="checkbox" id="flexCheckDefault" />
                        <label className="form-check-label" htmlFor="flexCheckDefault">
                          JPG
                        </label>
                      </div>
                      <div className="count">1009</div>
                      </Link>
                    <hr className="seperator" />
                    <p>Rarity</p>
                    <Link to="/">
                      <div className="form-check checkblock">
                          <input className="form-check-input" type="checkbox" id="flexCheckDefault" />
                        <label className="form-check-label" htmlFor="flexCheckDefault">
                          Base
                        </label>
                      </div>
                      <div className="count">6,666</div>
                    </Link>
                    <Link to="/">
                      <div className="form-check checkblock">
                          <input className="form-check-input" type="checkbox" id="flexCheckDefault" />
                        <label className="form-check-label" htmlFor="flexCheckDefault">
                          Rare
                        </label>
                      </div>
                      <div className="count">1,998</div>
                      </Link>
                    <Link to="/">
                      <div className="form-check checkblock">
                          <input className="form-check-input" type="checkbox" id="flexCheckDefault" />
                        <label className="form-check-label" htmlFor="flexCheckDefault">
                          Epic
                        </label>
                      </div>
                      <div className="count">198</div>
                      </Link>
                    <Link to="/">
                      <div className="form-check checkblock">
                        <input className="form-check-input" type="checkbox" id="flexCheckDefault" />
                        <label className="form-check-label" htmlFor="flexCheckDefault">Legendary</label>
                      </div>
                      <div className="count">18</div>
                    </Link>
                  </div>
                </li>
                {/* <li class="nav-item vcdropdown pricedropdown dropdown">
                              <a class="nav-link vcdropdown-toggle dropdown-toggle dropbtn" data-bs-toggle="dropdown" href="#" role="button" aria-expanded="false">Rarity <img src="assets/images/arrow-down.svg" alt="filter-icon" class="down-arrow"></a>
                              <div class="dropdown-content">
                                  <a href="#">
                                      <div class="form-check checkblock">
                                          <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault">
                                          <label class="form-check-label" for="flexCheckDefault">RARITY</label>
                                      </div>
                                      <div class="count"><img src="assets/images/arrow-down.svg" alt="filter-icon" class="down-arrow"></div>
                                  </a>
                                  <a href="#">
                                      <div class="form-check checkblock">
                                          <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault">
                                          <label class="form-check-label" for="flexCheckDefault">RARITY</label>
                                      </div>
                                      <div class="count"><img src="assets/images/arrow-down.svg" alt="filter-icon" class="down-arrow"></div>
                                  </a>
                                  <P>Rarity</P>
                                  <a href="#">
                                      <div class="form-check checkblock">
                                          <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault">
                                          <label class="form-check-label" for="flexCheckDefault">RARITY</label>
                                      </div>
                                      <div class="count"><img src="assets/images/arrow-down.svg" alt="filter-icon" class="up-arrow"></div>
                                  </a>
                                  <a href="#">
                                      <div class="form-check checkblock">
                                          <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault">
                                          <label class="form-check-label" for="flexCheckDefault">RARITY</label>
                                      </div>
                                      <div class="count"><img src="assets/images/arrow-down.svg" alt="filter-icon" class="up-arrow"></div>
                                  </a>
                                  <a href="#">
                                      <div class="form-check checkblock">
                                          <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault">
                                          <label class="form-check-label" for="flexCheckDefault">RARITY</label>
                                      </div>
                                      <div class="count"><img src="assets/images/arrow-down.svg" alt="filter-icon" class="up-arrow"></div>
                                  </a>
                                  <a href="#">
                                      <div class="form-check checkblock">
                                          <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault">
                                          <label class="form-check-label" for="flexCheckDefault">Legendary</label>
                                      </div>
                                      <div class="count"><img src="assets/images/arrow-down.svg" alt="filter-icon" class="up-arrow"></div>
                                  </a>
                              </div>
                            </li> */}
              </ul>
            </div>
          </div>
        </div>
        {/* Start of Price Filter Tab */}
        <div className="row">
          <div className="tab-content pricetabcontent" id="pills-tabContent">
            <div className="tab-pane fade" id="price-highest" role="tabpanel" aria-labelledby="pills-profile-tab">Price - Highest</div>
            <div className="tab-pane fade" id="rarity-rare" role="tabpanel" aria-labelledby="pills-contact-tab">Rarity - Rare</div>
            <div className="tab-pane fade" id="rarity-common" role="tabpanel" aria-labelledby="pills-contact-tab">Rarity - Common</div>
          </div>
        </div>
        {/* End of Price Filter Tab */}
      </div>
      {/* Slider */}
      <div className="container">
        <div className="row">
          <div className="ctab-content tab-content" id="pills-tabContent">
            <div className="tab-pane fade show active ctabpane" id="pills-home" role="tabpanel" aria-labelledby="pills-home-tab">
              <NftList data={NFTs} />
            </div>
            {/* END OF ALL TAB */}
          </div>
        </div>
      </div>
    </section>
    </NftProvider>
  );

};

export default VLADCollection;
